.root {
    position: relative;
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px;
    background-color: #fff;
    text-align: center;
}

.item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
    height: 70px;
    width: 20%;
    font-size: 14px;
    color: #555555;
    border-radius: 6px;
    transition: all 0.4s;
}

.icon {
    font-size: 30px;
}

.active {
    background-color: #ddfff2;
    border: 1px solid #008b5d;
    color: #008b5d;
}

.active::after {
    content: '✓';
    position: absolute;
    right: 1px;
    bottom: 1px;
    width: 10px;
    height: 10px;
    font-size: 10px;
    line-height: 10px;
    color: #008b5d;
}