.root {
    position: relative;
    padding-bottom: 50px;
    background: #f6f5f6;
}
.navHeader {
    position: fixed;
    width: 100%;
    z-index: 2;
}

.slides {
    z-index: 1;
    height: 252px;
}

.slides :global(a) {
    display: inline-block;
    width: 100%;
    height: 252px;
}

.slides :global(img) {
    width: 100%;
    height: 252px;
    vertical-align: top;
}

/* 房屋基础信息 */
.info {
    padding: 15px;
    background-color: #fff;
}

.infoTitle {
    font-weight: normal;
    font-size: 16px;
    color: #333;
}

.tags {
    position: relative;
    margin: 10px 0;
}

.tag {
    display: inline-block;
    padding: 4px 5px;
    margin-right: 5px;
    line-height: 12px;
    font-size: 12px;
    border-radius: 3px;
}

.tag1 {
    background: #e1f5f8;
    color: #39becd;
}

.tag2 {
    background: #e1f5ed;
    color: #39cd72;
}

.tag3 {
    background: #e6f2ff;
    color: #cd398a;
}

.tag4 {
    background: #e6f2ff;
    color: #e6c01b;
}

.infoPrice {
    border-top: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
    height: 80px;
}

.infoPriceItem {
    text-align: center;
    color: #999;
}
.infoPriceItem :global(div) {
    height: 26px;
    line-height: 26px;
}

.infoPriceItem :global(div:first-child) {
    color: #fa5741;
    font-size: 18px;
    font-weight: bolder;
}

.month {
    font-size: 12px;
    font-weight: normal;
}

.infoBasic {
    display: flex;
    line-height: 25px;
    align-items: center;
    margin: 10px 0;
}

.title {
    margin: 10px;
    font-size: 14px;
    color: #c01111;
    padding: 10px 0;
}
.map {
    position: relative;
    height: 250px;
}
.mapTitle {
    padding: 5px;
    font-size: 14px;
    color: #009687;
    line-height: 20px;
}
.mapContainer {
    height: 210px;
    margin: 5px 0 0 0;
}
.rect {
    position: absolute;
    height: 30px;
    width: 100px;
    padding: 0 3px;
    background: #a01d7fe6;
    line-height: 28px;
    white-space: nowrap;
    cursor: pointer;
    border-radius: 5px;
}

.arrow {
    display: block;
    width: 0;
    height: 0;
    margin: 0 auto;
    border: 4px solid transparent;
    border-top-width: 4px;
    border-top-color: #a01d7fe6;
}

.housename {
    display: inline-block;
    width: 90px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
}

.about {
    position: relative;
    background: #fff;
}
.titleEmpty {
    margin-bottom: 10px;
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    color: #888888;
}
.set {
    margin: 10px 0;
    padding: 0 10px;
    background: #fff;
    line-height: 150%;
}

.houseTitle {
    height: 30px;
    padding: 5px;
    margin: 5px;
    font-size: 16px;
    color: #c50d79;
}
.contact {
    position: relative;
    display: flex;
}

.user {
    display: flex;
    flex: 2;
    margin-bottom: 10px;
}

.user :global(img) {
    width: 50px;
    height: 50px;
    margin: 10px 10px 0 0;
}

.userInfo {
    padding: 15px 0 0 5px;
    color: tomato;
}
.userAuth {
    padding-top: 5px;
    color: rgb(197, 185, 10);
}
.userMsg {
    right: 15px;
    align-self: center;
    width: 70px;
    height: 30px;
    padding: 3px;
    border: 1px solid #009788;
    font-size: 16px;
    text-align: center;
    color: #009788;
    border-radius: 4px;
}
.descText {
    line-height: 1.6;
    padding: 10px 0;
    columns: #333;
}
.buttons {
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 70px;
    padding: 10px;
    background-color: #fff;
    text-align: center;
    line-height: 50px;
    font-size: 20px;
}
.favorite {
    background: #fff;
    flex: 1;
    color: #333;
    cursor: pointer;
    border-radius: 25px;
    border: 1px solid #333;
}
.favorite :global(img) {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}
.consult {
    flex: 1;
    margin: 0 10px;
    background: #59cfb6;
    border: 1px solid #333;
    border-radius: 25px;
    cursor: pointer;
}
.reserve {
    flex: 1;
    background: #078f71;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 25px;
    cursor: pointer;
}
.reserve :global(a) {
    color: #fff;
}